@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@500;600;700&display=swap);
body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  font-family: 'Spartan', sans-serif;
  color: rgb(1, 0, 36);
  line-height: 1.4;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.loading{
  height: 100vh;
    width: 100%;
    margin: 0;
    padding: 0;
}

.background{
  position: fixed;
  z-index: 3;
  background-color: rgb(0, 14, 31);
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.PopUp{
  position: fixed;
  z-index: 4;
  margin: none;
  justify-content: center;
  align-items: center;
}

.logo{
  justify-content: center;
  height: 18em;
  display: block;
  margin-left: auto;
  margin-right: auto;
  
}

.description{
  order: 1;
  font-size: 22px;
  color: white;
  text-align: center;
  margin: 1em;
  max-width: 18em;
}

.loader{
  z-index: 5;
  color: rgb(0, 37, 80);
  justify-content: center;
  text-align: center;
  padding: 1rem;
  width: 12rem;
  border-radius: 1rem;
  margin: auto;
  order: 2;
}
.App{
    height: 100vh;
    width: 100%;
    margin: 0;
    padding: 0;
}

Canvas{
    z-index: 1;
    position: relative;
    height: 100vh;
    width: 100vw;
    cursor:pointer;
}

.background2 {
    position: fixed;
    z-index: 3;
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

.background3 {
    background-color: rgb(0, 14, 31,1);
    height: 100vh;
    width: 100vw;
}


.exit{
    cursor: pointer;
    z-index: 5;
    color: rgb(0, 37, 80);
    justify-content: center;
    background-color: rgb(255, 255, 255);
    text-align: center;
    padding: 1rem;
    width: 14rem;
    font-size: 1.4rem;
    border-radius: 1rem;
    margin: auto;
    order: 2;
}

img{
    height: 20rem;
    width: auto;
}

.overlay{
    position: fixed;
    z-index: 3;
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

.overlay2{
    background-color: rgb(0, 14, 31,0.7);
    position: fixed;
    height: 100vh;
    width: 100vw;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}

.overlay3{
    position: fixed;
    z-index: 6;
    justify-content: center;
    text-align: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    height: 95%;
    width: 95%;
    border-radius: 1rem;
    background-color: rgb(220, 247, 255);
}

.overlay4{
    position: fixed;
    text-align: center;
    width: 100%;
    font-size: 2.6vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    z-index: 7;
    bottom: 1%;
    font-weight: 400;
    margin-top: auto;
    margin-bottom: auto;
    cursor: pointer;

}

.Modal{
    position: fixed;
    z-index: 6;
    justify-content: center;
    text-align: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    height: 88%;
    width: 100%;
    overflow: auto;
    background-color: white; 
    
}


.topBar{
    position: fixed;
    z-index: 7;
    top: 0%;
    display: flex;
    justify-content: center;
    height: 9vh;
    padding: 0.2rem;
    padding-top: 0.6rem;
    width: 100%;
    background-color:rgb(194, 241, 255);
    border-radius: 1rem 1rem 0px 0px;
}
/* .space{
    width: 33%;
    height: 100%;
    float: left;
} */

.title2{
    text-align: center;
    width: 100%;
    font-size: 3vh;
    float: left;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;

}
