.exitbutton{
    display: flex;
    cursor: pointer;
    width: 33%;
} */

.icon{
    height: 100%;
    width: auto;
    margin-left: auto; 
    margin-right: 0;
}

.text{
    display: grid;
    justify-content: center;
    text-align: left;
    font-size: 2.2vh;
    margin-right: auto;
    margin-left: auto;
    padding-right: 1.2rem;
    padding-left: 1.2rem;
    max-width: 50rem;
    margin-top: 44px;
    margin-bottom: 66px;
}

.webdevSlide{
    position: absolute;
    margin-top: 8%;
    font-size: 2vh;
    overflow: hidden;
}

.animationSlide{
    position: absolute;
    margin-top: 8%;
    font-size: 2vh;
}

.buttonBar {
    position: fixed;
    z-index: 7;
    width: 100%;
    background-color: blue;
    height: 6vh;
    top: 9vh;
    display: flex;
    align-content: center;
    align-items: center;
}

.webdevButton {
    color: white;
    height: 100%;
    width: 25rem;
    margin-left: auto;
    text-align: center;
    border-bottom: 4px solid pink;
    cursor: pointer;
    overflow: hidden;
    font-size: 2.2vh;
}

.animationButton{
    color: white;
    height: 100%;
    width: 25rem;
    margin-right: auto;
    text-align: center;
    border-bottom: 0px solid pink;
    cursor: pointer;
    overflow: hidden;
    font-size: 2.2vh;
}

